"use client";

import { useState } from "react";
import { motion } from "framer-motion";
import { useRouter } from "next/navigation";
import Navbar from "@/components/common/Navbar";

/* -----------------------------------------
   Custom Select Component (Drop-in)
--------------------------------------------*/
function CustomSelect({
  value,
  onChange,
  options,
}: {
  value: string;
  onChange: (v: string) => void;
  options: string[];
}) {
  const [open, setOpen] = useState(false);

  return (
    <div className="relative">
      {/* Trigger */}
      <div
        onClick={() => setOpen(!open)}
        className="mt-1 w-full rounded-2xl px-3 py-2.5 text-sm cursor-pointer flex justify-between items-center"
        style={{
          background: "var(--color-bg-neutral-50)",
          color: "var(--color-text-primary)",
          border: "1px solid var(--color-btn-teritary-200)",
        }}
      >
        {value}
        <span style={{ color: "var(--color-text-secondary)" }}>▼</span>
      </div>

      {/* Dropdown Menu */}
      {open && (
        <div
          className="absolute left-0  w-full rounded-xl shadow-lg z-50 overflow-hidden"
          style={{
            background: "var(--color-bg-neutral-50)",
            border: "1px solid var(--color-btn-teritary-200)",
          }}
        >
          {options.map((opt) => (
            <div
              key={opt}
              className="px-3 py-2 text-sm cursor-pointer hover:bg-[var(--color-line-blue-50)]"
              style={{
                color:
                  value === opt
                    ? "var(--color-text-primary)"
                    : "var(--color-text-secondary)",
              }}
              onClick={() => {
                onChange(opt);
                setOpen(false);
              }}
            >
              {opt}
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

/* -----------------------------------------
   Main Page
--------------------------------------------*/
export default function AvailableTimesPage() {
  const [selectedDays, setSelectedDays] = useState<string[]>([]);
  const [acceptUrgent, setAcceptUrgent] = useState(true);
  const [timeZone, setTimeZone] = useState("Eastern Time (ET)");
  const [responseTime, setResponseTime] = useState("Within 3hrs");

  const weekdays = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday"];
  const weekends = ["Saturday", "Sunday"];
  const everyday = [...weekdays, ...weekends];

  const router = useRouter();

  const toggleDay = (day: string) => {
    setSelectedDays((prev) =>
      prev.includes(day) ? prev.filter((d) => d !== day) : [...prev, day]
    );
  };

  const applyPreset = (days: string[]) => setSelectedDays(days);

  return (
    <div className="min-h-screen flex flex-col" style={{ background: "var(--background)" }}>
      <Navbar />

      {/* Center Container */}
      <div className="w-full flex justify-center mt-10 px-4">
        <div className="w-full max-w-[430px] flex flex-col">
          
          {/* Title */}
          <p
            className="text-[18px] font-semibold text-left"
            style={{ color: "var(--color-text-primary)" }}
          >
            Step 4 : Set your Available Times
          </p>

          {/* Progress Bar */}
          <div
            className="w-full h-[6px] rounded-full mt-3 mb-7"
            style={{ background: "var(--color-btn-teritary-200)" }}
          >
            <div
              className="h-full rounded-full"
              style={{ width: "66%", background: "var(--color-btn-primary-600)" }}
            />
          </div>

          {/* Card */}
          <motion.div
            initial={{ opacity: 0, y: 15 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.25 }}
            className="w-full rounded-2xl p-6"
            style={{
              background: "var(--color-bg-neutral-50)",
              border: "1px solid var(--color-btn-teritary-200)",
              boxShadow: "0 4px 18px rgba(10,10,10,0.08)",
            }}
          >
            {/* Time Zone + Responds Time */}
            <div className="flex gap-4 mb-6">
              <div className="flex-1">
                <label
                  className="text-sm font-medium"
                  style={{ color: "var(--color-text-secondary)" }}
                >
                  Time Zone*
                </label>

                <CustomSelect
                  value={timeZone}
                  onChange={setTimeZone}
                  options={[
                    "Eastern Time (ET)",
                    "Central Time (CT)",
                    "Mountain Time (MT)",
                    "Pacific Time (PT)",
                  ]}
                />
              </div>

              <div className="flex-1">
                <label
                  className="text-sm font-medium"
                  style={{ color: "var(--color-text-secondary)" }}
                >
                  Responds Time*
                </label>

                <CustomSelect
                  value={responseTime}
                  onChange={setResponseTime}
                  options={[
                    "Within 1hr",
                    "Within 3hrs",
                    "Within 6hrs",
                    "Within 12hrs",
                  ]}
                />
              </div>
            </div>

            {/* Quick Presets */}
            <div className="flex gap-2 flex-wrap items-center mb-6">
              <button
                onClick={() => applyPreset(weekdays)}
                className="px-4 py-1.5 rounded-full text-[13px]"
                style={{
                  border: "1px solid var(--color-btn-teritary-200)",
                  background: "var(--color-bg-neutral-50)",
                }}
              >
                Weekdays
              </button>

              <button
                onClick={() => applyPreset(weekends)}
                className="px-4 py-1.5 rounded-full text-[13px]"
                style={{
                  border: "1px solid var(--color-btn-teritary-200)",
                  background: "var(--color-bg-neutral-50)",
                }}
              >
                Weekends
              </button>

              <button
                onClick={() => applyPreset(everyday)}
                className="px-4 py-1.5 rounded-full text-[13px]"
                style={{
                  border: "1px solid var(--color-btn-teritary-200)",
                  background: "var(--color-bg-neutral-50)",
                }}
              >
                Everyday
              </button>

              <span
                className="ml-auto text-sm cursor-pointer"
                style={{ color: "var(--color-btn-primary-600)" }}
                onClick={() => setSelectedDays([])}
              >
                Clear
              </span>
            </div>

            {/* Days List */}
            <div className="flex flex-col gap-3 mb-6">
              {everyday.map((day) => (
                <label
                  key={day}
                  className="flex items-center justify-between rounded-xl px-4 py-2.5 transition"
                  onClick={() => toggleDay(day)}
                  style={{
                    border: selectedDays.includes(day)
                      ? "1px solid var(--color-btn-primary-600)"
                      : "1px solid var(--color-btn-teritary-200)",
                    background: selectedDays.includes(day)
                      ? "var(--color-line-blue-50)"
                      : "var(--color-bg-neutral-50)",
                  }}
                >
                  <span
                    className="text-sm font-medium"
                    style={{ color: "var(--color-text-primary)" }}
                  >
                    {day}
                  </span>

                  <input
                    type="checkbox"
                    readOnly
                    checked={selectedDays.includes(day)}
                    className="h-4 w-4"
                    style={{cursor: "pointer", accentColor: "var(--color-btn-primary-600)" }}
                  />
                </label>
              ))}
            </div>

            {/* Urgent Requests */}
            <div
              className="rounded-xl px-4 py-4 flex justify-between items-center"
              style={{
                background: "var(--color-bg-secondary-50)",
                border: "1px solid var(--color-btn-teritary-200)",
              }}
            >
              <div>
                <p
                  className="text-sm font-semibold"
                  style={{ color: "var(--color-text-primary)" }}
                >
                  Accept Urgent Requests
                </p>
                <p
                  className="text-xs"
                  style={{ color: "var(--color-text-muted)" }}
                >
                  Get notified for urgent jobs even outside your working hours
                </p>
              </div>

              <div
                onClick={() => setAcceptUrgent(!acceptUrgent)}
                className="w-12 h-6 rounded-full flex items-center cursor-pointer transition"
                style={{
                  background: acceptUrgent
                    ? "var(--color-success-line)"
                    : "var(--color-btn-teritary-300)",
                }}
              >
                <div
                  className="bg-white w-5 h-5 rounded-full shadow transform transition"
                  style={{
                    transform: acceptUrgent
                      ? "translateX(24px)"
                      : "translateX(4px)",
                  }}
                />
              </div>
            </div>
          </motion.div>

          {/* Buttons */}
          <div className="flex w-full justify-between mt-6 mb-10">
            <button
              className="cursor-pointer w-[48%] py-2.5 rounded-xl text-[15px]"
              style={{
                background: "var(--color-bg-neutral-50)",
                border: "1px solid var(--color-btn-teritary-200)",
              }}
            >
              Back
            </button>

            <button
              onClick={() => router.push("/verification")}
              className="cursor-pointer w-[48%] py-2.5 rounded-xl text-[15px]"
              style={{
                background: "var(--color-btn-primary-600)",
                color: "var(--color-bg-neutral-50)",
                boxShadow: "0 4px 14px rgba(21,93,252,0.25)",
              }}
            >
              Next
            </button>
          </div>

        </div>
      </div>
    </div>
  );
}
